import { useCallback, useState } from 'react';
import { REACT_APP_HOST_HOOKS } from '../config/config.js';

export const useUpdateTemplate = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const updateTemplate = useCallback(async (templateId, formDataObject, files) => {
    setLoading(true);
    setError(null);

    const formData = new FormData();
    formData.append('name', formDataObject.name);
    formData.append('content', formDataObject.content);

    const pubs = formDataObject.assignedPublications
      ? formDataObject.assignedPublications.split(',').map(s => s.trim())
      : [];

    pubs.forEach(pub => formData.append('assignedPublications', pub));
    files.forEach(file => formData.append('images-posventa', file));

    try {
      const res = await fetch(`${REACT_APP_HOST_HOOKS}/api/templates/${templateId}`, {
        method: 'PUT',
        body: formData
      });

      if (!res.ok) throw new Error('Error al actualizar la plantilla');

      const data = await res.json();
      return data;
    } catch (err) {
      setError(err.message);
      throw err;
    } finally {
      setLoading(false);
    }
  }, []);

  return { updateTemplate, loading, error };
};