import React, { useState, useEffect } from 'react';
import { REACT_APP_HOST_HOOKS } from '../../config/config.js';
import Modal from "react-modal";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import '../../styles/templates/templateManager.css';
import useCreateTemplate from "../../hooks/useCreateTemplate.js";
import { useUpdateTemplate } from '../../hooks/useUpdateTemplate.js';

// Configura el elemento raíz para React Modal (asegúrate de que el selector coincida con el de tu app)
Modal.setAppElement('#root');

const TemplateManager = () => {
  const [templates, setTemplates] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [selectedImages, setSelectedImages] = useState([]);
  const [editImages, setEditImages] = useState([]);
  const createTemplate = useCreateTemplate();
  const { updateTemplate } = useUpdateTemplate();

  console.log(templates,'tmmm')
  // Estado para el formulario de creación.
  const [createFormData, setCreateFormData] = useState({
    name: '',
    content: '',
    assignedPublications: ''
  });
  
  // Estados para el modal de edición.
  const [editingTemplate, setEditingTemplate] = useState(null);
  const [editFormData, setEditFormData] = useState({
    name: '',
    content: '',
    assignedPublications: ''
  });

  // Estado para el buscador.
  const [searchTerm, setSearchTerm] = useState('');

  // Obtener las plantillas desde el backend.
  const fetchTemplates = async () => {
    try {
      const res = await fetch(`${REACT_APP_HOST_HOOKS}/api/templates`);
      if (!res.ok) {
        throw new Error('Error al obtener las plantillas');
      }
      const data = await res.json();
     
      setTemplates(data);

    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchTemplates();
  }, []);

  // Manejo de cambios para el formulario de creación.
  const handleCreateInputChange = (e) => {
    const { name, value } = e.target;
    setCreateFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Manejo de cambios para el formulario de edición.
  const handleEditInputChange = (e) => {
    const { name, value } = e.target;
    setEditFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Enviar formulario para crear nueva plantilla.
  const handleCreateSubmit = async (e) => {
      e.preventDefault();
    
      try {
      const newTemplate = await createTemplate(createFormData, selectedImages);

      setTemplates(prev => [...prev, newTemplate]);
      setCreateFormData({ name: '', content: '', assignedPublications: '' });
      setSelectedImages([]);

      toast.success("¡Creada correctamente!", {
        position: "top-right",
        autoClose: 3000, // Se cierra en 3 segundos
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        theme: "colored",
      });
  
    } catch (err) {
      alert(err.message);
    }
  };

  // Enviar formulario para actualizar plantilla (modal de edición).
  const handleEditSubmit = async (e) => {
    e.preventDefault();

    try {

      const updatedTemplate = await updateTemplate( 
        editingTemplate._id,
        editFormData,
        editImages);

      setTemplates(prev =>
        prev.map(t => t._id === updatedTemplate._id ? updatedTemplate : t)
      );
      
      closeModal();
    } catch (err) {
      alert(err.message);
    }
  };

  // Abre el modal de edición y configura el template a editar.
  const handleEdit = (template) => {
    setEditingTemplate(template);
    setEditFormData({
      name: template.name,
      content: template.content,
      assignedPublications: template.assignedPublications.join(', ')
    });
    setEditImages(template.attachments || []);
    setModalIsOpen(true);
  };

  const removeEditImage = (urlToRemove) => {
  setEditImages(prev => prev.filter(url => url !== urlToRemove));
  };

  // Cierra el modal de edición.
  const closeModal = () => {
    setModalIsOpen(false);
    setEditingTemplate(null);
    setEditFormData({ name: '', content: '', assignedPublications: '' });
    setEditImages([]);
  };

  // Manejo de la cancelación en el modal.
  const handleCancelEdit = () => {
    closeModal();
  };

  // Filtrar plantillas según el término de búsqueda.
  const filteredTemplates = templates.filter(template => 
    template.name.toLowerCase().includes(searchTerm.toLowerCase())
  );


  return (
    <div className="template-manager">
      <div className="container-manager-templates">
       
        <div className="template-list-container">

          <h2 className="template-form-title">Editar plantilla</h2>

          <div className="template-search-container">
            <input
              type="text"
              placeholder="Buscar por nombre..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="template-search-input"
            />
          </div>

          {loading ? (
            <p className="template-loading">Cargando plantillas...</p>
          ) : error ? (
            <p className="template-error">Error: {error}</p>
          ) : (
            <ul className="template-list">
              {filteredTemplates.map(template => (
                <li key={template._id} className="template-item">
                  <div>
                    <h3 className="template-name">{template.name}</h3>
                    <p className="template-content">{template.content}</p>
                    <p className="template-assignments">
                      <strong>Publicaciones asignadas:</strong> {template.assignedPublications.join(', ')}
                    </p>
                    <div className='container-preview-images-templates'>
                      {template.attachments.map(img => (
                        <img src={img} alt={img.name}></img>
                      ))}
                    </div>

                  </div>
                  <div className="container-btn-plantillas">
                    <button
                      className="template-edit-btn"
                      onClick={() => handleEdit(template)}
                      aria-label="Editar"
                    >
                      <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" viewBox="0 0 16 16">
                        <path d="M12.146.146a.5.5 0 0 1 .708 0l2 2a.5.5 0 0 1 0 .708l-9 9a.5.5 0 0 1-.168.11l-4 1.5a.5.5 0 0 1-.65-.65l1.5-4a.5.5 0 0 1 .11-.168l9-9zM11.207 2L3 10.207V13h2.793L14 4.793 11.207 2z"/>
                      </svg>
                    </button>
                  </div>
                </li>
              ))}
            </ul>
          )}
        </div>
  
        {/* Formulario para crear nueva plantilla */}
        <div className="template-create-container">
          <h2 className="template-form-title">Crear Nueva Plantilla</h2>
          <form className="template-form" onSubmit={handleCreateSubmit}>
            <div className="template-form-group">
              <label className="template-form-label">
                Nombre:
                <input
                  type="text"
                  name="name"
                  value={createFormData.name}
                  onChange={handleCreateInputChange}
                  required
                  className="template-form-input"
                />
              </label>
            </div>
            <div className="template-form-group">
              <label className="template-form-label">
                Contenido:
                <textarea
                  name="content"
                  value={createFormData.content}
                  onChange={handleCreateInputChange}
                  maxLength={350}
                  required
                  className="template-form-textarea"
                />
              </label>
            </div>

            <div className="template-form-group">
              <label className="template-form-label">
                Imagen:
                <input
                  type="file"
                  multiple
                  accept="image/*"
                  onChange={(e) => setSelectedImages(Array.from(e.target.files))}
                  className="template-form-input"
                />
              </label>
            </div>
          
            <button type="submit" className="template-submit-btn">
              Crear
            </button>
          </form>
        </div>
      </div>
  
      {/* Modal para editar plantilla */}
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        className="modal-content"
        overlayClassName="modal-overlay"
      >
        <div className="modal-inner">
          <h2 className="modal-title">Editar Plantilla</h2>
          <form className="template-form" onSubmit={handleEditSubmit}>
            <div className="template-form-group">
              <label className="template-form-label">
                Nombre:
                <input
                  type="text"
                  name="name"
                  value={editFormData.name}
                  onChange={handleEditInputChange}
                  required
                  className="template-form-input"
                />
              </label>
            </div>

            <div className="template-form-group">
              <label className="template-form-label">
                Contenido:
                <textarea
                  name="content"
                  value={editFormData.content}
                  onChange={handleEditInputChange}
                  maxLength={350}
                  required
                  className="template-form-textarea"
                />
              </label>
            </div>

            {/* —— Sección de preview de imágenes —— */}
            <div className="template-form-group">
              <label className="template-form-label">Imágenes adjuntas:</label>
              <div className="container-preview-images-templates">
                {editImages.map(url => (
                  <div key={url} className="edit-image-item">
                    <img src={url} alt="" className="edit-image-thumb" />
                    <button
                      type="button"
                      className="remove-image-btn"
                      onClick={() => removeEditImage(url)}
                    >
                      ✕
                    </button>
                  </div>
                ))}
                {editImages.length === 0 && <p>No hay imágenes adjuntas.</p>}
              </div>
            </div>

            <div className="template-form-group">
              <label className="template-form-label">
                Imagen:
                <input
                  type="file"
                  multiple
                  accept="image/*"
                  onChange={(e) => setSelectedImages(Array.from(e.target.files))}
                  className="template-form-input"
                />
              </label>
            </div>

            <div className="modal-buttons">
              <button type="submit" className="template-submit-btn">
                Actualizar
              </button>
              <button type="button" className="template-cancel-btn" onClick={handleCancelEdit}>
                Cancelar
              </button>
            </div>
          </form>
        </div>
      </Modal>
      <ToastContainer />
    </div>
  );
};

export default TemplateManager;
