import { useCallback } from "react";

const useCreateTemplate = () => {
  const createTemplate = useCallback(async (formValues, selectedImages) => {
    const formData = new FormData();
    formData.append("name", formValues.name);
    formData.append("content", formValues.content);
    formData.append("assignedPublications", formValues.assignedPublications);

    selectedImages.forEach(image => {
      formData.append("images-posventa", image);
    });

    const response = await fetch(`${process.env.REACT_APP_HOST_HOOKS}/api/templates`, {
      method: "POST",
      body: formData,
    });

    if (!response.ok) {
      throw new Error("Error al crear la plantilla");
    }

    return await response.json();
  }, []);

  return createTemplate;
};

export default useCreateTemplate;