import React, { useState } from 'react';
import { REACT_APP_HOST_HOOKS } from "../../config/config.js";
import { IoPower } from 'react-icons/io5';
import '../../styles/login/logoutButton.css'; 

const LogoutButton = () => {
  const [showConfirm, setShowConfirm] = useState(false); 
  const [isLoggedOut, setIsLoggedOut] = useState(false); 

  const handleLogout = async () => {
    try {
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/auth/logout`, {
        method: 'POST',
        credentials: 'include', // Asegúrate de enviar las cookies
      });

      if (response.ok) {
        
        setIsLoggedOut(true); // Indicar que se ha deslogueado
        window.location.href = '/'; // Redirige al usuario al inicio
      } else {
        console.error("Error al cerrar sesión");
      }
    } catch (error) {
      console.error("Error al desloguearse:", error.message);
    }
  };

  return (
    <div className="logout-bubble">
      {/* Burbuja de encendido/apagado */}
      <button className={`logout-btn ${isLoggedOut ? 'off' : 'on'}`} onClick={() => setShowConfirm(true)}>
        <IoPower className={`logout-icon ${isLoggedOut ? 'off-icon' : 'on-icon'}`} />
      </button>

      {/* Cartel de confirmación */}
      {showConfirm && (
        <div className="confirm-logout">
          <p>¿Estás seguro que deseas cerrar sesión?</p>
          <button className="confirm-btn" onClick={handleLogout}>Sí</button>
          <button className="cancel-btn" onClick={() => setShowConfirm(false)}>No</button>
        </div>
      )}
    </div>
  );
};


export default LogoutButton;