import React, { useState } from "react";
import useGetSavedProducts from "../../hooks/useGetSavedProducts";
import useGetTemplates from "../../hooks/useGetTemplates";
import { REACT_APP_HOST_HOOKS } from "../../config/config";
import { FiTrash, FiArrowUp, FiArrowDown } from "react-icons/fi";
import Modal from "react-modal";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { ClipLoader } from "react-spinners";
import "../../styles/products/savedProducts.css";

const SavedProducts = () => {
  const { products, error, loading, reloadProducts } = useGetSavedProducts();
  const { templates, error: templatesError, loading: templatesLoading } = useGetTemplates();
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [selectedProductId, setSelectedProductId] = useState(null);
  const [selectedTemplateIds, setSelectedTemplateIds] = useState([]);
  const [isProcessing, setIsProcessing] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [secondModalOpen, setSecondModalOpen] = useState(false);
  const [selectedSecondTemplateIds, setSelectedSecondTemplateIds] = useState([]);
  const [delaysAsign, setDelaysAsign] = useState({});

  const openModal = (productId) => {
    setSelectedProductId(productId);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setSelectedProductId(null);
    setSelectedTemplateIds([]);
  };

  const handleDeleteTemplate = async (productId, templateId) => {
    try {
      setIsProcessing(true);
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/${productId}/templates/${templateId._id}`, {
        method: "DELETE",
      });

      if (!response.ok) {
        throw new Error("Error al eliminar la plantilla.");
      }

      await reloadProducts();

      toast.success("¡Plantilla eliminada correctamente.!", {
        position: "top-right",
        autoClose: 3000, 
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        theme: "colored",
      });
  

    } catch (error) {
      toast.error(error.message);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleAddTemplates = async () => {
    if (selectedTemplateIds.length === 0) {
      toast.warning("Por favor, selecciona al menos una plantilla.");
      return;
    }

    try {
      setIsProcessing(true);
     
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/${selectedProductId}/assign-template`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ templateIds: selectedTemplateIds }),
      });

      if (!response.ok) {
        throw new Error("Error al agregar la(s) plantilla(s).");
      }

      closeModal();
      await reloadProducts();
      
      toast.success("¡Plantilla(s) asignada(s) correctamente.!", {
        position: "top-right",
        autoClose: 3000, // Se cierra en 3 segundos
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        theme: "colored",
      });
  
    } catch (error) {
      toast.error(error.message);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleMoveTemplate = async (productId, templateId, direction) => {
    try {
      const response = await fetch(
        `${REACT_APP_HOST_HOOKS}/api/products/${productId}/templates/reorder`,
        {
          method: "PATCH",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ templateId, direction }),
        }
      );
  
      if (!response.ok) {
        throw new Error("Error al reordenar la plantilla.");
      }

      await reloadProducts();
    } catch (error) {
      toast.error(error.message);
    }
  };

  const buttonStyle = {
    background: "#dc3545",
    color: "#fff",
    border: "none",
    padding: "5px 10px",
    borderRadius: "5px",
    cursor: "pointer"
  };

const handleQuitProduct = async (productId) => {
  const confirmDelete = () => {
    deleteProduct(productId);
  };

  toast.info(
    <div>
      <p>¿Estás seguro de que quieres eliminar este producto?</p>
      <p>Se eliminara de la base de datos, pero se conserva en MELI.</p>
      <div style={{ display: "flex", justifyContent: "center", gap: "10px" }}>
        <button onClick={confirmDelete} style={buttonStyle}>Sí, eliminar</button>
        <button onClick={() => toast.dismiss()} style={buttonStyle}>Cancelar</button>
      </div>
    </div>,
    {
      position: "top-center",
      autoClose: false,
      closeOnClick: false,
      draggable: false
    }
  );
};

  const deleteProduct = async (productId) => {
    try {
      const response = await fetch(
        `${REACT_APP_HOST_HOOKS}/api/products/${productId}/saved-product`,
        {
          method: "DELETE",
          headers: { "Content-Type": "application/json" }
        }
      );
  
      if (!response.ok) {
        throw new Error("Error al eliminar el producto.");
      }
  
      await reloadProducts();
      toast.success("Producto eliminado correctamente.");
    } catch (error) {
      toast.error(error.message);
    }
  };

  const toggleTemplateSelection = (templateId) => {
    setSelectedTemplateIds((prevSelected) => {
      if (prevSelected.includes(templateId)) {
        return prevSelected.filter((id) => id !== templateId);
      } else {
        return [...prevSelected, templateId];
      }
    });
  };

  // SECONDS MESSAGES

  const openSecondModal = (productId) => {
    setSelectedProductId(productId);
    setSecondModalOpen(true);
    setSelectedSecondTemplateIds([]); 
  };

  const closeSecondModal = () => {
    setSecondModalOpen(false);
    setSelectedProductId(null);
    setSelectedSecondTemplateIds([]);
  };

  const handleDeleteSecondTemplate = async (productId, templateId) => {
    try {
      setIsProcessing(true);
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/${productId}/second-template/${templateId._id}`, {
        method: "DELETE",
      });

      if (!response.ok) {
        throw new Error("Error al eliminar la plantilla.");
      }

      await reloadProducts();

      toast.success("¡Plantilla eliminada correctamente.!", {
        position: "top-right",
        autoClose: 3000, 
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        theme: "colored",
      });
  

    } catch (error) {
      toast.error(error.message);
    } finally {
      setIsProcessing(false);
    }
  };

  const toggleSecondTemplate = (templateId) => {
    setSelectedSecondTemplateIds(prev =>
      prev.includes(templateId)
        ? prev.filter(id => id !== templateId)
        : [...prev, templateId]
    );
  };

  const handleAddSecondMessages = async () => {
    try {
      setIsProcessing(true);
      const response = await fetch(
        `${REACT_APP_HOST_HOOKS}/api/templates/${selectedProductId}/assign-second-messages`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ templateIds: selectedSecondTemplateIds }),
        }
      );
      if (!response.ok) throw new Error("Error al asignar second messages");
      closeSecondModal();
      await reloadProducts();
      toast.success("¡Second messages asignadas!", { });
    } catch (err) {
      toast.error(err.message);
    } finally {
      setIsProcessing(false);
    }
  };
  
  const refetchProductId = async (productId) => {
    try {
      setIsProcessing(true);

      const response = await fetch(
        `${REACT_APP_HOST_HOOKS}/api/products/${productId}/refetch-id`,
        {
          method: "GET",
          headers: { "Content-Type": "application/json" },
        }
      );

      if (!response.ok) throw new Error("Error al asignar delay");

      const data = await response.json();

      if(data)
      setDelaysAsign((prev) => ({
        ...prev,
        [productId]: data.secondMessageDelay,
      }));

      return data;
    } catch (err) {
      toast.error(err.message);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleApplyDelay = async (productId, delayHours) => {
    try {
      setIsProcessing(true);

      const response = await fetch(
        `${REACT_APP_HOST_HOOKS}/api/products/${productId}/assign-delay`,
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ delayHours }),
        }
      );

      if (!response.ok) throw new Error("Error al asignar delay");

      await refetchProductId(productId);

      toast.success("¡Delay aplicado!", {});
    } catch (err) {
      toast.error(err.message);
    } finally {
      setIsProcessing(false);
    }
  };


  if (loading) return <ClipLoader color="#007bff" size={50} className="spinner" />;
  if (error) return <p className="error-text">Error: {error}</p>;

  const filteredProducts = products
  .map((product, index) => ({ ...product, index })) // Agregar índice
  .filter(product => product.title.toLowerCase().includes(searchTerm.toLowerCase()))
  .sort((a, b) => b.index - a.index);

  return (
    <div className="saved-products-container">
      <h1 className="page-title">Productos Guardados</h1>

      <div className="template-search-container">
        <input
          type="text"
          placeholder="Buscar por nombre..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="template-search-input"
        />
      </div>

      <div className="container-items-saved">

        {filteredProducts.map((product) => (
          <div key={product.id} className="product-item-save-products">
            <h3 className="product-title">{product.title}</h3>
            <p className="product-id">ID Producto: {product.id}</p>

            <div className="container-father-templates-show">

              <div className="container-child-templates-show">
                
                <h3 className="templates-title">Plantillas asignadas: </h3>
                <div className="content-child-templates-show"> 
                
                  {/* Si el producto tiene variantes, mostramos cada una */}
                  {product.variations && product.variations.length > 0 ? (

                    product.variations.map((variation) => (
                      <div key={variation.id} className="variant-section">
                      <h5 className="variant-title">
                        Variante:{" "}
                        {variation.attribute_combinations && variation.attribute_combinations.length > 0
                          ? variation.attribute_combinations.map(attr => `${attr.name}: ${attr.value_name}`).join(" | ")
                          : variation.name
                        }
                      </h5>
                      {variation.templates && variation.templates.length > 0 ? (
                        <ul className="template-list-asign">
                          {variation.templates.map((template, index) => (
                            <li key={template.templateId} className="template-item-products-save">
                              <div className="template-info">
                                <strong>{template.name}</strong>
                              </div>
                              <div className="container-buttons-products-saved">
                            <div className="move-buttons">
                              <button
                                className="move-template-btn"
                                onClick={() => handleMoveTemplate(product.id, template.templateId, "up")}
                                disabled={index === 0}
                                title="Mover hacia arriba"
                              >
                                <FiArrowUp size={16} color="#007bff" />
                              </button>
                              <button
                                className="move-template-btn"
                                onClick={() => handleMoveTemplate(product.id, template.templateId, "down")}
                                disabled={index === variation.templates.length - 1}
                                title="Mover hacia abajo"
                              >
                                <FiArrowDown size={16} color="#007bff" />
                              </button>
                            </div>
                            <button
                              className="delete-template-btn"
                              onClick={() => handleDeleteTemplate(product.id, template.templateId)}
                            >
                              <FiTrash size={20} color="#ff4d4d" />
                            </button>
                              </div>
                            </li>
                          ))}
                        </ul>
                      ) : (
                        <p className="no-templates-msg">No hay plantillas asignadas para esta variante.</p>
                      )}
                      </div>
                    ))
                  ) : (
                  // Caso de producto sin variantes: mostrar plantillas asignadas globalmente
                    <ul className="template-list-asign">
                      {product.templates.map((template, index) => (
                        <li key={template.templateId} className="template-item-products-save">
                          <div className="template-info">
                            <strong>{template.name}</strong>
                          </div>
                          <div className="container-buttons-products-saved">
                      <div className="move-buttons">
                        <button
                          className="move-template-btn"
                          onClick={() => handleMoveTemplate(product.id, template.templateId, "up")}
                          disabled={index === 0}
                          title="Mover hacia arriba"
                        >
                          <FiArrowUp size={16} color="#007bff" />
                        </button>
                        <button
                          className="move-template-btn"
                          onClick={() => handleMoveTemplate(product.id, template.templateId, "down")}
                          disabled={index === product.templates.length - 1}
                          title="Mover hacia abajo"
                        >
                          <FiArrowDown size={16} color="#007bff" />
                        </button>
                      </div>
                      <button
                        className="delete-template-btn"
                        onClick={() => handleDeleteTemplate(product.id, template.templateId)}
                      >
                        <FiTrash size={20} color="#ff4d4d" />
                      </button>
                          </div>
                        </li>
                      ))}
                    </ul>
                  )}
                    
                  <button className="open-modal-btn" onClick={() => openModal(product.id)}>
                    Asignar plantilla
                  </button>
                
                  <button className="quit-modal-btn" onClick={() => handleQuitProduct(product.id)}>
                  Quitar producto
                  </button>
                </div>
              </div>

              <div className="container-child-templates-show container-second-message">
                <div className="content-titles-second-messages">
                  <h3 className="templates-title">Plantillas c/ delay:</h3>
                  <div className="delay-container">
                    <label htmlFor={`delayHours-${product.id}`} className="delay-label">
                      Delay (horas)
                    </label>
                    <div className="delay-input-group">
                    <input
                      type="number"
                      id={`delayHours-${product.id}`}
                      min={1}
                      max={72}
                      defaultValue={product.secondMessageDelay ?? 24}
                      className="delay-input"
                      aria-describedby={`defaultDelay-${product.id}`}
                    />
        
                    {/* 2) Botón: cuando hagan click, leo el value con document.getElementById */}
                    <button
                      type="button"
                      className="delay-apply-btn"
                      onClick={() => {
                        // leo el input en el DOM
                        const el = document.getElementById(`delayHours-${product.id}`);
                        const horas = Number(el.value) || 0;
                        handleApplyDelay(product.id, horas);
                      }}
                      disabled={isProcessing}
                    >
                      Aplicar cambios
                    </button>

                      {/* Texto o input read‐only para mostrar el valor por defecto */}
                      <span
                        id={`defaultDelay-${product.id}`}
                        className="delay-default"
                        title="Valor asignado actualmente."
                      >
                        ↺ {delaysAsign[product.id] ?? product.secondMessageDelay } h
                      </span>
                    </div>
                  </div>
                </div>

                <div className="content-child-second-message-show">
                  <ul>
                    {product.secondMessages?.length > 0 ?
                      product.secondMessages.map((template, index) => (
                        
                      <li key={template.templateId}>
                        <div className="template-info">
                          <strong>{template.name}</strong>
                        </div>
                        <button
                          className="delete-template-btn"
                          onClick={() => handleDeleteSecondTemplate(product.id, template.templateId)}
                        >
                          <FiTrash size={20} color="#ff4d4d" />
                        </button>
                      </li>
                      )):(
                        <h4 className="empty-second-messages-title">Aún no se asignaron plantillas con delay</h4>
                      )
                    }
                  </ul>
                  
                  <button
                    className="open-second-modal-btn"
                    onClick={() => openSecondModal(product.id)}
                  >
                    Asignar segundo mensaje
                  </button>
                </div>
              </div>

            </div>

          </div>
        ))}
      </div>

      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        className="modal-content"
        overlayClassName="modal-overlay"
      >
        <h2>Seleccionar Plantillas</h2>
        {templatesLoading ? (
          <ClipLoader size={20} color="#007bff" />
        ) : templatesError ? (
          <p className="error-text">Error: {templatesError}</p>
        ) : (
          <div className="templates-checkbox-list">
            {templates.map((template) => (
              <label key={template._id} className="template-checkbox-label">
                <input
                  type="checkbox"
                  value={template._id}
                  checked={selectedTemplateIds.includes(template._id)}
                  onChange={() => toggleTemplateSelection(template._id)}
                />
                {template.name}
              </label>
            ))}
          </div>
        )}
        <div className="modal-buttons-products-saved">
          <button className="add-template-btn" onClick={handleAddTemplates} disabled={isProcessing}>
            {isProcessing ? <ClipLoader size={15} color="white" /> : "Agregar"}
          </button>
          <button className="close-modal-btn" onClick={closeModal}>
            Cerrar
          </button>
        </div>
      </Modal>

      <Modal
          isOpen={secondModalOpen}
          onRequestClose={closeSecondModal}
          className="modal-content"
          overlayClassName="modal-overlay"
        >
          <h2>Seleccionar segundo mensaje</h2>

          {templatesLoading ? (
            <ClipLoader size={20} color="#007bff" />
          ) : templatesError ? (
            <p className="error-text">Error: {templatesError}</p>
          ) : (
            <div className="templates-checkbox-list">
              {templates.map((template) => (
                <label key={template._id} className="template-checkbox-label">
                  <input
                    type="checkbox"
                    value={template._id}
                    checked={selectedSecondTemplateIds.includes(template._id)}
                    onChange={() => toggleSecondTemplate(template._id)}
                  />
                  {template.name}
                </label>
              ))}
            </div>
          )}

          <div className="modal-buttons-products-saved">
            <button
              className="add-template-btn"
              onClick={handleAddSecondMessages}
              disabled={isProcessing}
            >
              {isProcessing ? <ClipLoader size={15} color="white" /> : "Asignar segundo mensaje"}
            </button>
            <button className="close-modal-btn" onClick={closeSecondModal}>
              Cerrar
            </button>
          </div>
        </Modal>
      <ToastContainer />
    </div>
  );
};

export default SavedProducts;
