import { useState, useEffect } from "react";
import { ClipLoader } from "react-spinners";
import { REACT_APP_HOST_HOOKS } from "../../config/config.js";
import '../../styles/templates/templateModal.css';

const TemplateModal = ({ product, closeModal }) => {
  const [templates, setTemplates] = useState([]);
  const [selectedTemplates, setSelectedTemplates] = useState([]);
  const [selectedVariation, setSelectedVariation] = useState(null);
  const [loadingTemplates, setLoadingTemplates] = useState(false);

  // Al cargar el producto, se fija la variante inicial y se obtienen las plantillas globales
  useEffect(() => {
    if (!product?.id) return;

    if (product.variations && product.variations.length > 0) {

      setSelectedVariation(prev => prev || product.variations[0].id);
    } else {
      setSelectedVariation(null);
    }

    const fetchTemplates = async () => {
      try {
        const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/templates`);
        const data = await response.json();
        setTemplates(data);
      } catch (error) {
        console.error("Error al cargar las plantillas globales:", error);
      }
    };

    fetchTemplates();
  }, [product.id, product.variation]);

  // Cada vez que se cambia la variante o se carga el producto, se actualiza el listado de plantillas asignadas
  useEffect(() => {
    if (!product?.id) return;
    setLoadingTemplates(true);
    setSelectedTemplates([]); // Limpiar mientras se carga

    const fetchAssignedTemplates = async () => {
      try {
        const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/template/${product.id}`);
        if (!response.ok) {
          console.warn("Producto no encontrado en la base de datos.");
          setLoadingTemplates(false);
          return;
        }
        const data = await response.json();
        let assigned = [];
    
        if (selectedVariation && data.variations && Array.isArray(data.variations)) {
          const variation = data.variations.find(v => v.id === selectedVariation);
          if (variation && variation.templates) {
            assigned = variation.templates.map(t => t.templateId.toString());
          }
        } else if (data.templates && Array.isArray(data.templates)) {
          assigned = data.templates.map(t => t.templateId.toString());
        }
        setSelectedTemplates(assigned);
      } catch (error) {
        console.error("Error al cargar las plantillas asignadas:", error);
      } finally {
        setLoadingTemplates(false);
      }
    };

    fetchAssignedTemplates();
  }, [product.id, selectedVariation]);
  // Maneja la selección o deselección de una plantilla
  const handleTemplateSelection = (templateIdStr) => {
    setSelectedTemplates(prevSelectedTemplates => {
      if (prevSelectedTemplates.includes(templateIdStr)) {
        return prevSelectedTemplates.filter(id => id !== templateIdStr);
      } else {
        return [...prevSelectedTemplates, templateIdStr];
      }
    });
  };

  // Envía la asignación de plantillas, incluyendo el id de la variante (si corresponde)
  const handleAssignTemplates = async () => {
    try {
      const selectedVarObject = product.variations?.find(v => String(v.id) === String(selectedVariation));

      const variationName = selectedVarObject 
        ? selectedVarObject.attribute_combinations?.map(attr => `${attr.name}: ${attr.value_name}`).join(" | ") 
        : "Sin nombre";

      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/${product.id}/assign-templates-modal`, {
        method: "POST",
        headers: {"Content-Type": "application/json"},
        body: JSON.stringify({
          templateIds: selectedTemplates,
          productAsign: product.title,
          variationId: selectedVariation,
          variationName, 
        }),
      });
      if (response.ok) {
        closeModal();
      } else {
        console.error("Error al asignar plantillas");
      }
    } catch (error) {
      console.error("Error al asignar las plantillas:", error);
    }
  };

  return (
    <div className="template-manager-modal">
      
      <div className="template-manager-content">
        <h2 className="template-manager-title">Asignar plantillas a {product.title}</h2>
      
              
        {product.variations && product.variations.length > 0 && (
          <div className="variation-selector">
            <label htmlFor="variation-select" className="variation-label">
              Selecciona la variante:
            </label>
            <select
              id="variation-select"
              value={selectedVariation}
              onChange={(e) => setSelectedVariation(e.target.value)}
              className="variation-select"
            >
              {product.variations.map(variation => (
                <option key={variation.id} value={variation.id}>
                  {variation.attribute_combinations && variation.attribute_combinations.length > 0 
                    ? variation.attribute_combinations.map(attr => `${attr.name}: ${attr.value_name}`).join(" | ")
                    : variation.id
                  }
                </option>
              ))}
            </select>
          </div>
        )}

        <h3 className="template-manager-subtitle">Plantillas disponibles</h3>
        {loadingTemplates ? (
          <div className="template-manager-spinner">
            <ClipLoader color="#007bff" />
          </div>
        ) : (
          <ul className="template-manager-list">
            {templates.map(template => (
              <li key={template._id} className="template-manager-item">
                <input
                  type="checkbox"
                  checked={selectedTemplates.includes(template._id.toString())}
                  onChange={() => handleTemplateSelection(template._id.toString())}
                  className="template-manager-checkbox"
                />
                <h3>{template.name}:</h3> <p>{template.content}</p> 
              </li>
            ))}
          </ul>
        )}
        
        <div className="container-modal-product-list">
          <button onClick={handleAssignTemplates} className="template-manager-btn template-manager-btn-asign">Asignar Plantillas</button>
          <button className="template-manager-btn template-manager-btn-close" onClick={closeModal}>
            Cerrar
          </button>
        </div>

      </div>
    </div>
  );
};

export default TemplateModal;
